import {Basket} from "@/model/Basket";
import {MutationTree} from "vuex";
import {State} from "@/store/index";

export enum MutationType {
    AddBasket = "addBasket",
    UpdateBasket = "updateBasket",
    InitBaskets = "initBaskets",
}

interface Mutations {
    addBasket(state: State, basket: Basket): void;
    updateBasket(state: State, basket: Basket): void;
    initBaskets(state: State, baskets: Basket[]): void;
}

export const mutations: MutationTree<State> & Mutations = {
    addBasket(state, basket) {
        state.baskets.push(basket);
    },

    updateBasket(state, basket) {
        const index = state.baskets.findIndex(b => b.id === basket.id);

        console.log("Mutation.updateBasket: Index to update is " + basket.id);

        if (index != -1) {
            state.baskets.splice(index, 1, basket);
        }
    },

    initBaskets(state, baskets) {
        state.baskets = baskets;
    }
};
