import {Revisable, RevisionType} from "@/model/Revisable";


export enum Ownership {
    PRIVATE,
    PUBLIC
}

export class Basket extends Revisable {

    public id?: number;
    public name: string;
    public description: string;
    public image?: string;
    public positions: Array<number>;

    public owner?: number;
    public ownership: Ownership = Ownership.PRIVATE;

    constructor(name = "", description = "") {
        // TODO get correct infos for revision
        super("todo", new Date, RevisionType.CREATED);
        this.name = name;
        this.description = description;
        this.positions = [];
    }
}