import {Basket} from "@/model/Basket";
import {ActionTree} from "vuex";
import BasketDBService from "@/service/BasketDBService";
import {MutationType} from "@/store/mutations";
import {State} from "@/store/index";
import ArticleDBService from "@/service/ArticleDBService";
import CatalogWebService from "@/service/CatalogWebService";

export enum ActionType {
    LoadCatalog = "loadCatalog",

    CreateBasket = "createBasket",
    StoreBasketImage = "storeBasketImage",
    InitBaskets = "initBaskets",
}

interface Actions {
    loadCatalog({commit}: any, payload: { catalogName?: string }): void;

    createBasket({commit}: any, basket: Basket): void;

    storeBasketImage({commit}: any, payload: { basketId: number, imageDataUrl: string }): void;

    initBaskets({commit}: any): void;
}

export const actions: ActionTree<State, State> & Actions = {

    loadCatalog({commit}, payload: { catalogName: string }) {
        CatalogWebService.loadCatalog(payload.catalogName).then((articles) => {
            ArticleDBService.store(articles).then((count: number) => {
                console.log("Fetched " + count + " articles from catalog " + payload.catalogName + " on server and put to local database.");
            })
        })
    },

    createBasket({commit}, basket) {
        BasketDBService.create(basket).then((basketId) => {
            basket.id = basketId;
            commit(MutationType.AddBasket, basket);
            console.log("Basket created: " + basketId);
        })
    },

    storeBasketImage({commit}, payload: { basketId: number, imageDataUrl: string }) {
        console.log("Action.storeBasketImage: " + JSON.stringify(payload));

        BasketDBService.getBasket(payload.basketId).then((basket) => {
            if (basket) {
                basket.image = payload.imageDataUrl;
                BasketDBService.update(basket).then(() => {
                    commit(MutationType.UpdateBasket, basket);
                })
            }
        })
    },

    initBaskets({commit}) {
        const initialBaskets: Array<Basket> = [
            new Basket('Supermarket XXL', 'Mon, 12. July 2021'),
            new Basket('My Favourite Shop', 'Tue, 22. July 2021'),
            new Basket('Local Fresh Discounter', 'Mon, 12. July 2021'),
            new Basket('Weekly Market', 'Fri, 29. July 2021'),
            new Basket('DIY Store', 'Mon, 12. July 2021')
        ];

        BasketDBService.storeData(initialBaskets).then((count: number) => {
            BasketDBService.getBaskets().then((baskets) => {
                commit(MutationType.InitBaskets, baskets);
            })
        })
    },
};
