import {Account} from "@/model/Account";

export enum LocalStorageItemKey {
    ACCOUNT_NAME = "account.name",
    ACCOUNT_ORGANIZATION = "account.organization",
    ACCOUNT_SERVER_URL = "account.serverUrl"
}

class LocalStorageService {
    public static readonly DEFAULT_SERVER_URL = 'http://localhost:8081';

    get localStorageSupported(): boolean {
        return ('localStorage' in window);
    }

    get localStorageInitialized(): boolean {
        if (this.localStorageSupported) {
            return localStorage.getItem(LocalStorageItemKey.ACCOUNT_NAME) !== null;
        }
        return false;
    }

    saveAccount(account: Account): void {
        if (this.localStorageSupported) {
            localStorage.setItem(LocalStorageItemKey.ACCOUNT_NAME, account.name);
            localStorage.setItem(LocalStorageItemKey.ACCOUNT_ORGANIZATION, account.organization);
            localStorage.setItem(LocalStorageItemKey.ACCOUNT_SERVER_URL, account.serverUrl);
        }
    }

    getServerUrl(): string {
        const serverUrl = localStorage.getItem(LocalStorageItemKey.ACCOUNT_SERVER_URL);
        return serverUrl != null ? serverUrl : LocalStorageService.DEFAULT_SERVER_URL;
    }

    getAccount(): Account {
        if (this.localStorageSupported) {
            let name = "";
            let organization = "";
            let severUrl = LocalStorageService.DEFAULT_SERVER_URL;

            for (let i = 0; i < localStorage.length; i++) {
                const key = localStorage.key(i);
                const value = localStorage.getItem(key == null ? "" : key);

                if (key === LocalStorageItemKey.ACCOUNT_NAME) {
                    name = value == null ? name : value;
                }
                if (key === LocalStorageItemKey.ACCOUNT_ORGANIZATION) {
                    organization = value == null ? organization : value;
                }
                if (key === LocalStorageItemKey.ACCOUNT_SERVER_URL) {
                    severUrl = value == null ? severUrl : value;
                }
            }
            return new Account(name, organization, severUrl);

        } else {
            return new Account();
        }
    }

    clear(): void {
        if (this.localStorageSupported) {
            localStorage.clear();
        }
    }
}

export default new LocalStorageService();
