import axios, {AxiosResponse} from "axios";
import {Article} from "@/model/Article";
import LocalStorageService from "@/service/LocalStorageService";
import {ArticleResult} from "@/model/ArticleResult";
import {Catalog} from "@/model/Catalog";
import {CatalogResult} from "@/model/CatalogResult";

class CatalogWebService {

    public async getAvailableCatalogs(): Promise<Catalog[]> {
        const url = LocalStorageService.getServerUrl() + "/catalogs";
        const promise: Promise<AxiosResponse<CatalogResult>> = axios.get<CatalogResult>(url);
        return promise.then(axiosResponse => axiosResponse.data.catalogs)
            .catch((e: Error) => {
                console.error(e);
                return [] as Catalog[];
            });
    }

    public async loadCatalog(catalogName?: string): Promise<Article[]> {
        const url = LocalStorageService.getServerUrl() + "/articles";
        const promise: Promise<AxiosResponse<ArticleResult>> = axios.get<ArticleResult>(url, { params: { catalogName } });
        return promise.then(axiosResponse => axiosResponse.data.articles)
            .then(articles => articles.map(article => new Article(catalogName + "-" + article.id, article.name, article.description, article.base64Image, catalogName)))
            .catch((e: Error) => {
                console.error(e);
                return [] as Article[];
            });
    }
}

export default new CatalogWebService();
