import {createRouter, createWebHistory, RouteRecordRaw} from 'vue-router'
import BasketsOverview from '@/ui/baskets/BasketsOverview.vue'

const routes: Array<RouteRecordRaw> = [
    {
        path: '/settings',
        component: () => import('@/ui/settings/SettingsView.vue') // lazy-loaded when the route is visited
    },
    {
        path: '/catalogs',
        component: () => import('@/ui/catalogs/CatalogsView.vue') // lazy-loaded when the route is visited
    },
    {
        path: '/account',
        component: () => import('@/ui/account/AccountView.vue') // lazy-loaded when the route is visited
    },
    {
        path: '/about',
        component: () => import('@/ui/about/AboutView.vue') // lazy-loaded when the route is visited
    },
    {
        path: '/baskets/:basketId',
        props: true,
        component: () => import('@/ui/baskets/BasketView.vue') // lazy-loaded when the route is visited
    },
    {
        path: "/:catchAll(.*)",
        props: true,
        component: BasketsOverview
    }
]

const router = createRouter({
    history: createWebHistory(process.env.BASE_URL),
    routes
})

export default router
