export enum RevisionType {
    CREATED,
    CHANGED,
    DELETED
}

export class Revisable {
    private _revisor: string; // who did the revision?
    private _revisionDate: Date; // the timestamp of this revision
    private _revisionType: RevisionType; // the kind of revision done by the revisor

    constructor(revisor: string, revisionDate: Date, revisionType: RevisionType) {
        this._revisor = revisor;
        this._revisionDate = revisionDate;
        this._revisionType = revisionType;
    }


    get revisor(): string {
        return this._revisor;
    }

    get revisionDate(): Date {
        return this._revisionDate;
    }

    get revisionType(): RevisionType {
        return this._revisionType;
    }
}