
import {defineComponent, onMounted, onUnmounted, reactive} from 'vue'
import {Account} from "@/model/Account";

export default defineComponent({
  name: 'NavigationDrawer',

  props: {
    account: Account,
  },

  setup(props, {emit}) {
    const state = reactive({
      drawer: false,
      items: [
        {title: 'Baskets', icon: 'mdi-shopping-outline', route: '/home'},
        {title: 'Settings', icon: 'mdi-application-settings', route: '/settings'},
        {title: 'Catalogs', icon: 'mdi-book-open-variant', route: '/catalogs'},
        {title: 'Account', icon: 'mdi-account-edit-outline', route: '/account'},
        {title: 'About', icon: 'mdi-chat-question-outline', route: '/about'},
      ]
    });

    onMounted(() => {
      window.addEventListener('keydown', (e) => {
        if (e.code === 'Escape') {
          state.drawer = false
        }
      })
    });

    onUnmounted(() => {
      window.removeEventListener('keydown', (e) => {
        if (e.code === 'Escape') {
          state.drawer = false
        }
      })
    });

    const itemSelected = () => {
      emit("hide-drawer");
    }

    return {
      state,
      itemSelected,
    }
  }
})
