import {Basket} from "@/model/Basket";
import {BasketDB} from "@/db/BasketDB";

/**
 * CRUD service for Baskets.
 */
class BasketDBService {
    private db = new BasketDB();

    /**
     * Creates a new basket and adds it to the database. If an object with the same primary key already exist,
     * the operation will fail.
     */
    public async create(basket: Basket): Promise<number> {
        return this.db.baskets.add(basket);
    }

    public async getBaskets(): Promise<Array<Basket>> {
        return this.db.baskets.toArray();
    }

    public async getBasket(basketId: number): Promise<Basket | undefined> {
        return this.db.baskets.get(basketId);
    }

    /**
     * Updates existing basket in the database with given changes.
     */
    public async update(basket: Basket) {
        if (basket.id) {
            this.db.baskets.update(basket.id, basket);
        }
    }

    public async delete(basketId: number) {
        this.db.baskets.delete(basketId);
    }

    public async clear() {
        this.db.baskets.clear();
    }

    /**
     * Add all given objects to the store, replacing existing.
     */
    public async storeData(baskets: Basket[]): Promise<number> {
        return this.db.baskets.bulkPut(baskets);
    }


}

export default new BasketDBService();